
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video,
input {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font-weight: normal;
  vertical-align: baseline;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
a {
  color: #7e8c8d;
  -webkit-backface-visibility: hidden;
  text-decoration: none;
}
li {
  list-style: none;
}
body {
  -webkit-text-size-adjust: none;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
}
.header {
  width: 100%;
  z-index: 9999;
}
.header .for-sticky {
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 50px;
  opacity: 0.6;
  z-index: 1;
  top: 0;
  left: 0;
  border-bottom: 1px solid #f1f1f1;
  display: block;
  padding: 0px 15px 5px 15px;
  background: #fff;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.header .for-sticky .logo a {
  color: #2f2f2f;
  text-decoration: none;
}
.header .for-sticky .logo a .logo-nav {
  padding: 6px 0 10px 0;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  width: 45px;
  display: block;
  max-width: 100%;
  vertical-align: middle;
  border: 0;
}
.header .menu-button {
  width: 34px;
  height: 45px;
  position: fixed;
  right: 0;
  margin-top: 13px;
  margin-right: 35px;
  z-index: 1000;
  padding: 0;
  cursor: pointer;
  background: transparent;
  border: none;
}
.header .menu-button span {
  display: block;
  position: absolute;
  height: 1px;
  width: 100%;
  background: #2f2f2f;
  borer-radius: 9px;
  opacity: 1;
  left: 0;
}
.header .menu-button span:nth-child(1) {
  top: 0px;
}
.header .menu-button span:nth-child(2) {
  top: 9px;
}
.header .menu-button span:nth-child(3) {
  top: 18px;
}
.content-wrap {
  height: 100%;
  width: 100%;
}
.content-wrap .content {
  position: absolute;
  width: 100%;
  height: 100%;
}
.preloader {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #fff;
  z-index: 999999;
}
.status {
  width: 200px;
  height: 200px;
  position: absolute;
  left: 50%;
  top: 50%;
  background-image: url(./static/img/loader.044748c.gif);
  background-repeat: no-repeat;
  background-position: center center;
  text-align: center;
  margin: -40px 0 0 -100px;
}
.status img {
  text-align: center;
  margin-top: -80px;
}
#home {
  display: block;
/*background: url("~common/images/hero1.jpg")*/
  background-size: cover;
  min-width: 100%;
  height: 100%;
  height: 100%;
  width: 100%;
}
#home h1 {
  line-height: 40px;
  letter-spacing: 0.6px;
  font-size: 36px;
  margin-bottom: 10px;
  font-weight: bold;
}
#home .typed {
  display: block;
  font-weight: 400;
  font-size: 16px;
  line-height: 26px;
  letter-spacing: 1.5px;
  margin: 0 auto;
  opacity: 0.6;
}
#home .hero-content {
  position: absolute;
  text-align: center;
  min-width: 300px;
  left: 50%;
  top: 50%;
  padding: 65px;
  background: rgba(255,255,255,0.8);
  outline: 4px double #fff;
  outline-offset: 8px;
  -webkit-transform: translateX(-50%) translateY(-50%);
  transform: translateX(-50%) translateY(-50%);
}
.typed-element {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.typed-element .typed-cursor {
    opacity: 1;
    -webkit-animation: typedjsBlink 0.7s infinite;
            animation: typedjsBlink 0.7s infinite;
}
@-webkit-keyframes typedjsBlink {
50% {
    opacity: 0.0;
}
}
@keyframes typedjsBlink {
50% {
    opacity: 0.0;
}
}
